// export default Notes
import React, { useState, useEffect } from 'react'
import { PDFObject } from 'react-pdfobject'
import Modal from 'react-bootstrap/Modal'
import { Card, Button, Icon, Grid } from 'semantic-ui-react'

import socket from '../../socket'
import UploadNotes from './UploadNotes'
import CentreButton from '../CentreButton'
import { baseUrl } from '../../config'
import axios from '../../api'

const Notes = () => {
  const [notes, setNotes] = useState([])
  const [open, setOpen] = useState(false)
  const [noteId, setNoteId] = useState(null)

  useEffect(() => {
    socket.on('NOTE::NEW', (data) => {
      console.log('NOTE::NEW :', data)
      if (data.status) {
        setNotes([data.noteData, ...notes])
      } else {
        // TODO: handle false
      }
    })

    return () => {
      socket.removeAllListeners('NOTE::NEW')
    }
  }, [notes])

  useEffect(() => {
    ;(async () => {
      const { data } = await axios.post('/notes/list')
      console.log('/notes/list :', data)
      if (data.status) {
        setNotes([...data.noteData])
      } else {
        // TODO: handle false
      }
    })()
  }, [])

  return (
    <>
      <Modal show={open} backdrop={true} keyboard={false} animation={false}>
        <Modal.Body>
          <UploadNotes handleClose={setOpen} />
        </Modal.Body>
      </Modal>
      <Modal
        show={noteId !== null}
        // fullscreen={true}
        keyboard={false}
        animation={false}
        size={'lg'}
        // style={{ width: '100%' }}
        dialogClassName="modal-90w"
      >
        <Modal.Body>
          <Grid columns={1}>
            <Grid.Column>
              <Grid.Row>
                <PDFObject
                  width={'100%'}
                  height={'500px'}
                  url={baseUrl + '/notes/get/' + noteId}
                />
              </Grid.Row>
              <Grid.Row>
                <Button
                  color={'red'}
                  onClick={() => {
                    setNoteId(null)
                  }}
                  style={{
                    marginLeft: 'auto',
                    marginRight: 'auto',
                    width: '50%',
                  }}
                >
                  Close
                </Button>
              </Grid.Row>
            </Grid.Column>
          </Grid>
        </Modal.Body>
      </Modal>
      <CentreButton>
        <Button
          primary
          onClick={() => setOpen(true)}
          style={{ marginBottom: 10 }}
        >
          <Icon name={'add'} /> <h4>Upload Notes</h4>
        </Button>
      </CentreButton>
      <div
        style={{
          backgroundColor: 'whitesmoke',
          padding: '10px',
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          overflow: false,
        }}
      >
        {notes.map((note) => {
          let noteTitle = note.noteName
          if (noteTitle.length > 100) {
            noteTitle = noteTitle.slice(0, 100) + '...'
          }
          return (
            <Card
              key={note.noteId}
              color="red"
              style={{
                width: '60%',
                display: 'flex',
              }}
            >
              <Card.Content textAlign={'center'} header={noteTitle} />
              <Card.Description>
                <Grid columns="one">
                  <Grid.Column textAlign={'center'}>
                    <Grid.Row textAlign={'center'} style={{ width: '60%' }}>
                      <span>{note.noteDescription}</span>
                    </Grid.Row>
                  </Grid.Column>
                </Grid>
              </Card.Description>
              <Card.Content>
                <Grid columns={1}>
                  <Grid.Column textAlign={'center'}>
                    <Button
                      color={'grey'}
                      variant="contained"
                      onClick={() => setNoteId(note.noteId)}
                      size={'medium'}
                      style={{ width: '80%' }}
                    >
                      View / Download Notes
                    </Button>
                  </Grid.Column>
                </Grid>
              </Card.Content>
            </Card>
          )
        })}
      </div>
    </>
    // <div>
    // <CentreButton>
    //   <Button primary onClick={() => setOpen(true)}>
    //     <Icon name={'add'} /> <h4>Upload Notes</h4>
    //   </Button>
    // </CentreButton>
    //   {open ? (
    //     <Modal show={open} keyboard={false} animation={false}>
    //       <UploadNotes handleClose={setOpen} />
    //     </Modal>
    //   ) : null}
    //   {notes.map((note) => {
    //     return (
    //       <Card>
    //         <h1>{note.noteName}</h1>
    //         <h2>{note.noteDescription}</h2>
    // <PDFObject
    //   height={'500px'}
    //   url={baseUrl + '/notes/get/' + note.noteId}
    // />
    //       </Card>
    //     )
    //   })}
    // </div>
  )
}

export default Notes
